@import "../../../assets/styles/app";

.notifications {
  position: relative;
  width: 48px;
  height: 48px;

  .refWrapper {
    width: 48px;
    height: 48px;

    .btn {
      position: relative;
      width: 48px;
      height: 48px;
      border-right: 1px solid $color_lines;

      &::before {
        display: block;
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 6px;
        height: 6px;
        background: $color_main;
      }

      &_light {
        border-color: $color_light_version_lines;
      }

    }
  }

  .menu {
    position: absolute;
    z-index: 999;
    left: 0;
    width: 360px;
    bottom: 0;
    transform: translateY(100%);
    height: 200px;
    background-color: green;
  }


}