@import "../../../assets/styles/app";

.points {


  .header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 36px;
    border-bottom: 1px solid $color_lines;

    @include desktop {
      grid-template-columns: 1fr 210px;
    }

    p {
      display: flex;
      align-items: center;
      padding-left: 12px;
      color: $color_main_100;
      font-weight: 400;
      font-size: 12px;
      line-height: 140%;


      &:nth-child(2) {
        color: $color_body_50;

        @include desktop {
          padding-left: 64px;
        }
      }
    }
  }

  .list {


    .item {
      height: 48px;
      border-bottom: 1px solid $color_lines;
      display: grid;
      grid-template-columns: repeat(2, 1fr);

      @include desktop {
        grid-template-columns: 1fr 210px;
      }

      .left, .right {
        display: flex;
        align-items: center;
        padding-left: 12px;
      }

      .left {

        img {
          display: block;
          width: 32px;
          height: 32px;
        }

        p {
          margin-left: 8px;
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          text-transform: uppercase;
          color: $color_body_50;
        }
      }

      .right {

        @include desktop {
          padding-left: 64px;
        }

        p {
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          color: $color_main_100;
        }
      }
    }
  }

  .btn {
    height: 48px;
    width: 100%;

    @include desktop {
      margin-top: 24px;
      width: 318px;
    }
  }


}